import "./navbar.css"
import {Link} from "react-router-dom"
import {useState} from 'react'

// import Dropdown from '../dropdown/Dropdown'
import {FaBars, FaTimes} from "react-icons/fa"



export default function Navbar() {
	const [click, setClick] = useState(false)
	const [dropdown, setDropdown] = useState(false)
	
	const handleClick = () => setClick(!click)
	const closeMobileMenu = () => setClick(false)

	const onMouseEnter = () => {
		if (window.innerWidth < 960) {
			setDropdown(false);
		} else {
			setDropdown(true)
		}
	}
	const onMouseLeave = () => {
		if (window.innerWidth < 960) {
			setDropdown(false);
		} else {
			setDropdown(false)
		}
	}

	return (
		<div className="Navbar">
			<div className="navbarWrapper">
				<Link to='/' className="link">
                    <span className="logo">TA.</span>
				</Link>
				<div className="navMenuBtn" onClick={handleClick}>
					{click? <FaTimes className="bars"/> :<FaBars className="bars"/>}
					</div>
					
				<ul className= {click ? 'navMenu active' : 'navMenu'}>
						 
                 <li className="navItem"><Link to="/About" className="link" onClick={closeMobileMenu}>about</Link></li>
					<li className="navItem" onMouseEnter={onMouseEnter}
						onMouseLeave={onMouseLeave}>
						
						<Link to='/' className="link" onClick={closeMobileMenu}>My patrons</Link>
							{/* <Link to='/my patrons' className="link" onClick={closeMobileMenu}>my patrons<FaCaretDown/>
						    </Link>
							
							{dropdown && <Dropdown/>} */}
							</li>
                 <li className="navItem"><Link to="/article" className="link" onClick={closeMobileMenu}>article</Link></li>
				 <li className="navItem"><Link to="/contact" className="link" onClick={closeMobileMenu}>contact</Link></li>
					</ul>
				</div>
				</div>
		
			
	)
}
