import React from 'react'
import "./article.css"
import N3928 from "../../img/N3928.jpg"

import { useState } from 'react'

export default function Article({ articles }) {
	const [readMore, setReadMore] = useState(false);
	return (
		<div className="article">
			<h3 className="articleTitle">Articles by Azeez</h3>
			{articles.map((articleItem) => {
				const { title, id , info} = articleItem;
			
			return (
			 <div key={id} className="articleContainer">
				
				
					<img src={N3928} alt="" className="draya" />
				<p className="articleContent">
					<h2>{title}</h2>
						{readMore ? info : `${info.substring(0, 300)}...`}
						<button className="readmore" onClick={() => setReadMore(!readMore)}>{ readMore ? "show less" : " read more"}</button>
					</p>
					<button className="next">next</button>
				</div>


			)
			
				})}
			</div>
		
	)
}
